@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  outline-color: #002afc;
}

body {
  /* background-image: url("file:///C:/Users/dhara/Downloads/pexels-photo-4946589%20copy.png"); */
  background-size: cover;
  /* background: linear-gradient(to bottom, #36d1dc , #5b86e5); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 105%;
}

.background-video {
  position: relative;
  width: 100vw;
  height: 99.8vh;
  overflow: hidden;
}

.background-video video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 1;
}

.content {
  position: relative;
  z-index: 1;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  margin-top: 5%;
  margin-left: 5%;
}

#container {
  width: 600px;
  padding: 32px;
  border-radius: 8px;
  background-color: #f60000;
  box-shadow: 2px 4px 8px #d7d9dc40;
  text-align: center;
}

td, th {
  padding: 5px;
  border: 1px solid #f9f9f9;
  text-align: left;
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background-color: #e2e8f0;
}

#addbtn, #viewbtn {
  margin-top: 5%;
}

#addbtn {
  margin-right: 2%;
}

p {
  font-size: 14px;
  color: #6b7280;
}

.signup-form {
  width: 480px;
  padding: 32px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 2px 4px 8px #6b728040;
  text-align: left;
  margin-left: 0px;
  margin-top: 50px;
  margin-bottom: 100px;
}

.login-form {
  top: 2px;
  width: 480px;
  padding: 32px;
  border-radius: 8px;
}

.header {
  margin-bottom: 48px;
}

.header h1 {
  font-weight: bolder;
  font-size: 28px;
  color: #80ff00;
}

.input {
  position: relative;
  margin-bottom: 24px;
}

.input input {
  width: 100%;
  border: none;
  padding: 8px 40px;
  border-radius: 4px;
  background-color: #f3f4f6;
  color: #1f2937;
  font-size: 16px;
}

.input input::placeholder {
  color: #698dd5;
}

.input i {
  top: 50%;
  width: 36px;
  position: absolute;
  transform: translateY(-50%);
  color: #6b7280;
  font-size: 16px;
}

#actions {
  width: 80px;
  color: #5b06f8;
  position: relative;
  font-size: 14px;
  font-weight: bold;
  transition: all .3s ease;
}

.signup-btn {
  width: 100%;
  border: none;
  padding: 8px 0;
  margin: 24px 0;
  border-radius: 4px;
  background-color: #63f187;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

.e-signup-btn {
  border: none;
  padding: 8px 0;
  margin: 24px 0;
  border-radius: 4px;
  background-color: #b9fd00;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  width: 45%;
}

.e-cancel-btn {
  border: none;
  padding: 8px 0;
  margin: 24px 0;
  border-radius: 4px;
  background-color: #6366f1;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  width: 45%;
}

#viewbtn, #addbtn {
  border: none;
  padding: 10px 0;
  padding: 10px;
  border-radius: 4px;
  background-color: #6366f1;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

#logout {
  border: none;
  padding: 10px 0;
  padding: 10px;
  margin-top: 2%;
  border-radius: 4px;
  background-color: #f01010;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

.signup-btn:active, #viewbtn:active, #addbtn:active {
  background-color: #000000;
  transition: all 0.3s ease;
}

.cancel-btn:active {
  background-color: #000000;
  transition: all 0.3s ease;
}
